.product-card {
  border: 1px solid #ccc;
  width: 200px;
  margin: 16px;
  padding: 16px;
}

.cart-item {
  align-items: center;
  margin-bottom: 10px;
  display: flex;
}

.cart-item img {
  width: 80px;
  margin-right: 10px;
}

.button-container {
  gap: 10px;
  display: flex;
}

.cart-button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  transition: background-color .3s;
}

.cart-button:hover {
  background-color: #45a049;
}

nav {
  justify-content: flex-start;
  margin: 20px 0;
  display: flex;
}

.nav-button {
  color: #000;
  cursor: pointer;
  background-color: silver;
  border: none;
  border-radius: 5px;
  margin: 0 10px;
  padding: 15px 25px;
  font-size: 18px;
  transition: background-color .3s, transform .2s;
}

.nav-button:hover {
  background-color: #a9a9a9;
  transform: scale(1.05);
}

input[type="text"] {
  border: 1px solid silver;
  border-radius: 5px;
  margin-right: 10px;
  padding: 15px;
  font-size: 18px;
}

.thank-you-message {
  text-align: center;
  color: #333;
  margin: 20px 0;
  font-size: 60px;
}

.logo {
  width: 100px;
  height: 20px;
  margin-right: 20px;
}
